<template>
  <div class="container">
    <div class="container-content">
      <el-row :gutter="20" class="container-top">
        <el-col v-for="obj in infoList" :key="obj.prop" :span="4">
          <CtTooltip :label="obj.label" :content="itemInfoFormatter(obj)"></CtTooltip>
        </el-col>
      </el-row>
      <div class="container-bottom">
        <div class="container-form">
          <ct-section-title class="content-info-title" title="关联号码" />
          <el-form ref="qryParamRef" :model="qryParam" size="small">
            <el-row :gutter="20">
              <el-col :span="5">
                <el-form-item label="ICCID" prop="iccid">
                  <el-input
                    v-model="qryParam.iccid"
                    :placeholder="inputPh('ICCID')"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="5">
                <el-form-item :label="$t('accountMag.simStatus')" prop="statusCd">
                  <ct-select
                    v-model="qryParam.statusCd"
                    :options="statusOptions"
                    :placeholder="selectPh('accountMag.simStatus')"
                  ></ct-select>
                </el-form-item>
              </el-col>
              <el-col :span="14">
                <el-form-item>
                  <div class="search-button-group">
                    <el-button
                      type="primary"
                      icon="el-icon-search"
                      @click="loadData(false)"
                      >{{ $t('common.query') }}</el-button
                    >
                    <el-button icon="el-icon-refresh" @click="reset">{{
                      $t('common.reset')
                    }}</el-button>
                  </div>
                </el-form-item>
              </el-col>
            </el-row>
            <div v-compPriv="34" class="tool-button-group">
              <el-button type="primary" icon="el-icon-plus" @click="batchClick(1000)">{{
                $t('common.batchImport')
              }}</el-button>
              <el-button
                icon="el-icon-delete"
                type="danger"
                plain
                @click="batchClick(1001)"
              >
                {{ $t('common.batchDelete') }}</el-button
              >
            </div>
          </el-form>
          <el-table ref="table" v-loading="loading" :data="tableData" :border="true">
            <el-table-column
              prop="iccid"
              label="ICCID"
              show-overflow-tooltip
            ></el-table-column>
            <el-table-column
              prop="msisdn"
              label="MSISDN"
              show-overflow-tooltip
            ></el-table-column>
            <el-table-column
              prop="statusCd"
              :label="$t('accountMag.simStatus')"
              :formatter="rowFormatter"
              show-overflow-tooltip
            ></el-table-column>
            <el-table-column :label="$t('common.action')" fixed="right">
              <template slot-scope="scope">
                <div class="action-button-group">
                  <el-button
                    v-compPriv="37"
                    type="text"
                    class="cor-red"
                    @click="clickDelete(scope.row)"
                    >{{ $t('common.delete') }}</el-button
                  >
                </div>
              </template>
            </el-table-column>
          </el-table>
          <el-pagination
            v-show="tableData.length > 0"
            background
            :current-page="page.pageIndex"
            align="right"
            :page-sizes="[10, 20, 50, 100]"
            :page-size="page.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.total"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <BatchUpload
      v-if="showUpload"
      :visible.sync="showUpload"
      :query-id="queryId"
      download-url="/order/download/"
      upload-url="/simCard/upload"
      :max-file-size="10 * 1024 * 1024"
      :param="{ userId }"
      :title="uploadTitle"
    >
      <template #tip>
        1、{{ $t('batchUpload.uploadTip1') }}<br />
        2、{{ $t('batchUpload.uploadTip2') }}<br />
        3、{{ $t('batchUpload.uploadTip3') }}<br />
        4、{{ $t('batchUpload.uploadTip4') }}
      </template>
    </BatchUpload>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin'
import BatchUpload from '@/components/BatchUpload'
import fetch from '@/request/fetch'
import { statusOptions } from '@/utils/constant'
export default {
  components: {
    BatchUpload,
  },
  mixins: [langMixin],
  props: {},
  data() {
    return {
      showUpload: false,
      queryId: '',
      userId: this.$route.query.userId,
      tableData: [],
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      detailInfo: {},
      activeTab: '1',
      qryParam: {
        accNum: '',
        statusCd: '',
      },
    }
  },
  computed: {
    statusOptions() {
      return statusOptions.map((item) => {
        return {
          ...item,
          label: this.$t('simMag.' + item.key),
        }
      })
    },
    infoList() {
      return [
        {
          label: this.$t('accountMag.account'),
          prop: 'userCode',
        },
        {
          label: this.$t('accountMag.userName'),
          prop: 'userName',
        },
        {
          label: this.$t('accountMag.email'),
          prop: 'email',
        },
        {
          label: this.$t('accountMag.phone'),
          prop: 'phone',
        },
        {
          label: this.$t('accountMag.fax'),
          prop: 'fax',
        },
        {
          label: this.$t('common.remark'),
          prop: 'remark',
        },
      ]
    },
    uploadTitle() {
      return this.queryId === 1000
        ? this.$t('common.batchImport')
        : this.$t('common.batchDelete')
    },
  },
  mounted() {
    this.queryDetail()
    this.loadData(false)
  },
  methods: {
    rowFormatter(row, col, val) {
      const item = this.statusOptions.find((item) => item.value === val)
      if (item) {
        return this.$t('simMag.' + item.key)
      }
      return val
    },
    async queryDetail() {
      const res = await fetch(
        '/user/querySystemUserDetail',
        {
          userId: this.userId,
          modifyFlag: 0,
        },
        { method: 'get' }
      )
      const obj = { ...res }
      if (obj.emailDesc) {
        obj.email = obj.emailDesc
      }
      if (obj.faxDesc) {
        obj.fax = obj.faxDesc
      }
      if (obj.mobileDesc) {
        obj.mobile = obj.mobileDesc
      }
      if (obj.phoneDesc) {
        obj.phone = obj.phoneDesc
      }
      if (obj.userNameDesc) {
        obj.userName = obj.userNameDesc
      }
      this.detailInfo = { ...obj }
    },
    async loadData(pageChangeFlag) {
      if (!pageChangeFlag) {
        this.page.pageIndex = 1
        this.page.pageSize = 10
      }
      const param = {
        userId: this.userId,
        ...this.qryParam,
        pageInfo: this.page,
      }
      this.loading = true
      const res = await fetch('/user/queryUserSimCardPage', param, {
        method: 'post',
      }).finally(() => {
        this.loading = false
      })
      this.tableData = res.result || []
      this.page.total = res.rowCount
    },
    itemInfoFormatter(obj) {
      const des = obj.formatter
        ? obj.formatter(this.detailInfo[obj.prop] ?? '-')
        : this.detailInfo[obj.prop] ?? '-'
      return des
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.loadData(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.loadData(true)
    },
    batchClick(queryID) {
      this.queryId = queryID
      this.showUpload = true
    },
    async clickDelete(row) {
      const param = { userId: this.userId }
      if (row.iccid) {
        param.type = 'iccid'
        param.accNums = [row.iccid]
      } else if (row.msisdn) {
        param.type = 'msisdn'
        param.accNums = [row.msisdn]
      }
      this.$confirm(this.$t('accountMag.deleteAsk'), {
        confirmButtonText: this.$t('common.yes'),
        cancelButtonText: this.$t('common.no'),
        type: 'warning',
      }).then(async () => {
        this.loading = true
        await fetch('/user/unBindSimCard', param, {
          method: 'post',
        }).finally(() => {
          this.loading = false
        })
        this.$message.success(this.$t('common.deleteSuccess'))
        this.loadData(false)
      })
    },
    reset() {
      this.$refs.qryParamRef.resetFields()
      this.loadData(false)
    },
  },
}
</script>
